<template>
  <b-container
    id="views-thedashboard_coupons"
    fluid
  >
    <b-button
      id="show-edit-coupons"
      class="m-3 float-right"
      @click="showEditCoupon(true)"
    >
      
    </b-button>

    <b-modal
      ref="edit-coupon-modal"
      size="lg"
      title="BootstrapVue"
    >
      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-coupon-title"></label>
        </b-col>
        <b-col sm="10">
          <b-form-input
            id="input-coupon-title"
            v-model="tempCoupon.title"
            placeholder=""
            size="sm"
          />
        </b-col>
      </b-row>

      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-coupon-percent"></label>
        </b-col>
        <b-col sm="10">
          <b-form-input
            id="input-coupon-percent"
            v-model="tempCoupon.percent"
            placeholder=""
            size="sm"
          />
        </b-col>
      </b-row>
      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-coupon-duedate"></label>
        </b-col>
        <b-col sm="10">
          <b-form-datepicker
            id="input-coupon-duedate"
            v-model="tempCoupon.due_date"
            class="mb-2"
          />
        </b-col>
      </b-row>
      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-coupon-code"></label>
        </b-col>
        <b-col sm="10">
          <b-form-input
            id="input-coupon-code"
            v-model="tempCoupon.code"
            placeholder=""
            size="sm"
          />
        </b-col>
      </b-row>

      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-coupon-isenabled"></label>
        </b-col>
        <b-col sm="10">
          <b-form-radio-group
            id="input-coupon-isenabled"
            v-model="tempCoupon.is_enabled"
            :options="isEnableOptions"
            name="input-coupon-isenabled"
          />
        </b-col>
      </b-row>

      <template v-slot:modal-footer>
        <div class="w-100">
          <b-button
            class="float-right"
            size="md"
            variant="primary"
            @click="updateCoupon"
          >
            
          </b-button>
          <b-button
            class="mr-1 float-right"
            size="md"
            variant="danger"
            @click="cancerEditCoupon"
          >
            
          </b-button>
        </div>
      </template>
    </b-modal>

    <b-table
      v-if="!isLoading"
      :items="coupons"
      :fields="fields"
      responsive
      small
      striped
      stacked="md"
    >

      <template v-slot:cell(is_enabled)="data">
        <p
          v-show="data.item.is_enabled"
          class="text-success"
        >
          
        </p>
        <p
          v-show="!data.item.is_enabled"
          class="text-danger"
        >
          
        </p>
      </template>

      <template v-slot:cell(actions)="row">
        <b-button
          class="mr-1"
          size="sm"
          @click="showEditCoupon(false,row.item)"
        >
          
        </b-button>
        <b-button
          class="mr-1"
          size="sm"
          @click="deleteCoupon(row.item)"
        >
          
        </b-button>
      </template>
    </b-table>

    <b-pagination-default
      :pagination="pagination"
      @paginate="getCoupons"
    />

  </b-container>
</template>

<script>
import { apiAdminGetCoupons, apiAdminDeleteCoupons } from "../plugins/axios"

export default {
  name: "TheDashboardCoupons",
  data() {
    return {
      coupons: [],
      fields: [
        {
          key: "title",
          label: ""
        },
        {
          key: "percent",
          label: ""
        },
        {
          key: "due_date",
          label: ""
        },
        {
          key: "is_enabled",
          label: ""
        },
        { key: "actions", label: "" }
      ],
      file: null,
      isEnableOptions: [
        { text: "", value: 1 },
        { text: "", value: 0 }
      ],
      isNew: false,
      pagination: {},
      tempCoupon: {
        title: "",
        percent: null,
        due_date: null,
        is_enabled: 1
      }
    }
  },
  computed: {
    loadingAmount() {
      return this.$store.state.loadingAmount
    },
    isLoading() {
      if (this.loadingAmount > 0) {
        return true
      } else {
        return false
      }
    }
  },
  mounted() {
    this.getCoupons()
  },
  methods: {
    showEditCoupon(isNew, item) {
      if (isNew) {
        this.tempCoupon = {}
        this.isNew = true
      } else {
        this.tempCoupon = Object.assign({}, item)
        this.isNew = false
      }
      this.$refs["edit-coupon-modal"].show()
    },
    cancerEditCoupon() {
      this.$refs["edit-coupon-modal"].hide()
    },
    deleteCoupon(item) {
      apiAdminDeleteCoupons(item).then(response => {
        this.getCoupons()
      })
    },
    updateCoupon() {
      let api = `/admin/coupon`
      let httpMethod = "post"
      if (!this.isNew) {
        api = `/admin/coupon/${this.tempCoupon.id}`
        httpMethod = "put"
      }
      this.axios[httpMethod](api, { data: this.tempCoupon }).then(response => {
        if (response.data.success) {
          this.$refs["edit-coupon-modal"].hide()
          this.getCoupons()
        } else {
          this.$refs["edit-coupon-modal"].hide()
          this.getCoupons()
          console.log("")
        }
      })
    },
    getCoupons(page = 1) {
      this.loading = true
      apiAdminGetCoupons(page).then(response => {
        this.loading = false
        this.coupons = response.data.coupons
        this.pagination = response.data.pagination
      })
    }
  }
}
</script>
